
header {
    display: flex;
    gap: 20px;
}

.flex{ 
    display: flex;
}

.flex section {
    flex-grow: 1 ;
}
a,
a:visited {
    color: blue;
}
aside ul,
ul.grid {
    padding: 0;
}
aside ul li {
    list-style: none;
}

aside ul li a {
    transition: all 1s linear;
}

aside ul li a.active {
    list-style: none;
    color: brown;
    font-size: 20px;
}

ul.grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    column-gap: 20px;
    row-gap: 20px;
}
ul.grid li article img {
    width: 100%;
}

ul.grid li.loading p,
ul.grid li.loading h4 {
    background-color: black;

}
.card {
    background-color: whitesmoke;
    padding: 30px;
    border-radius: 20px;
   
}


